html, body {
  background: #000000;
  color: #4cd137;
  margin:0;
  padding: 0;
  padding-top: 10px;
  font-family: 'Press Start 2P', cursive;
  text-align: center;
}

body {
  font-weight: 400;
}

button {
  font-family: 'Press Start 2P', cursive;
  display: block;
  margin: 0 auto;
  border: none;
  background: #a80020;
  background: #4cd137;
  padding: 10px 20px;
  text-transform: uppercase;
  border-radius: 0.5em;
  cursor: pointer;
}
button:disabled {
  cursor: not-allowed;
}
h1 {
  font-size: 20px;
  text-align:center;
  margin-bottom: 30px;
  margin-top: 20px;
}
h4{
  margin-top: 20px;
}

textarea {
  font-family: 'Press Start 2P', cursive;
  border: 3px solid black;
  border-radius: 20px;
  width: 90%;
  margin: 0 auto;
  outline: none;
  height: 200px;
  background: #4cd137;
  padding: 10px;
  margin-bottom: 15px;
}

textarea:disabled {
  background-color: #dcdde1;
}